import React, { useState } from 'react';
import {useParams} from 'react-router-dom';
import * as productActions from '../../redux/products/product.actions';
import * as productReducer from '../../redux/products/product.reducer';
import * as orderActions from '../../redux/orders/order.action';
import { useDispatch, useSelector } from 'react-redux';
import {useEffect} from 'react';
import Spinner from '../layout/Spinner'

interface URLParams{
    productId: string;
}

interface IProps{}
interface IState{
    productKey: productReducer.ProductState
}

let ProductDetails:React.FC<IProps> =  ({}) => {

    let [state, setState] = useState<{qty: number}>({
        qty: 0
    })

    let dispatch = useDispatch();
    let {productId} = useParams<URLParams>();

    let productState: productReducer.ProductState = useSelector((store: IState) =>{
        return store.productKey
    })

    useEffect(() => {
        dispatch(productActions.getProduct(productId))
    }, [productId])

    let addToCart = () => {
        dispatch(orderActions.addToCart({
            ...selectedProduct,
            qty: state.qty
        }))
    }

    let {loading, selectedProduct, errorMessage} = productState

    return (
        <React.Fragment>
            {/* {JSON.stringify(productState)} */}
            <section className="mt-4">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className=" h2 text-success">
                                Selected product
                            </p>
                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Modi quaerat quis excepturi ipsum repellendus eum facere fuga tempore omnis! Quod reprehenderit nisi hic culpa quo perferendis molestias esse amet deleniti.</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                loading ? <Spinner/> : <React.Fragment>
                    {
                        Object.keys(selectedProduct).length > 0 && 
                        <section className="mt-3">
                            <div className="container">
                                <div className="row">
                                <div className="col-md-4 text-center">
                                        <img src={selectedProduct.image} alt={''} className="shadow-lg"/>
                                    </div>
                                    <div className="col-md-8">
                                        <p className="h4">NAME : <span className="fw-bold">{selectedProduct.name}</span></p>
                                        <p className="h4">Brand : <span className="fw-bold">{selectedProduct.brand}</span></p>
                                        <p className="h4">Price : <span className="fw-bold">&#8377;{selectedProduct.price}</span></p>
                                            <div className="row">
                                                <div className="col-md-3">
                                                    {/* {JSON.stringify(state.qty)} */}
                                                    <select
                                                        required
                                                        value={state.qty}
                                                        onChange={(event) => {setState({qty: Number(event.target.value)})}}
                                                        className="form-control">
                                                        <option value="">Select Qty</option>
                                                        <option value="1">1</option>
                                                        <option value="2">2</option>
                                                        <option value="3">3</option>
                                                        <option value="4">4</option>
                                                        <option value="5">5</option>
                                                    </select>
                                                    <button className="btn btn-success btn-sm mt-2" onClick={addToCart}>Add To Cart</button>
                                                </div>
                                            </div>     
                                        <p className="h6 mt-2">Description: {selectedProduct.description}</p>
                                        <p className="h6">Usage: {selectedProduct.usage}</p>
                                    </div>
                                    </div>
                                </div>
                        </section>
                    }
                </React.Fragment>
            }
        </React.Fragment>
    );
}
export default ProductDetails;

