import React from 'react';
import * as alertActions from '../../redux/alert/alert.actions'
import * as alertReducer from '../../redux/alert/alert.reducer'
import { useSelector } from 'react-redux';


interface IProps{}
interface IState{
    alertKey: alertReducer.alertState
}

let Alert:React.FC<IProps> =  ({}) => {

    // get alert messages from Redux Store
    let alertState:alertReducer.alertState = useSelector((store : IState) => {
        return store.alertKey;
    });
    
    let {alerts} = alertState;

    return (
        <React.Fragment>
            {
                alerts && alerts.map(alert => {
                    return (
                        <div key={alert.id} className={`alert text-white d-flex align-items-center justify-content-center alert-dismissible animate__animated animate__slideInDown sticky-top ${alert.color}`} role="alert">
                            <i className="fa fa-check-circle"/>
                            <div>
                                &nbsp; {alert.message}
                            </div>
                            <button type="button" className="btn-close text-white float-end" data-bs-dismiss="alert" aria-label="Close"/>
                        </div>
                    )
                })
            }
        </React.Fragment>
    );
}
export default Alert;

