import React from 'react';
import * as orderActions from '../../redux/orders/order.action';
import * as orderReducer from '../../redux/orders/order.reducer';
import { NavLink } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

interface IProps{}
interface IState{
    orderKey: orderReducer.OrderState
}

const tax: number = 5;

let CartPage:React.FC<IProps> =  ({}) => {

    let dispatch = useDispatch()

    let cartstate: orderReducer.OrderState = useSelector((store: IState) => {
        return store.orderKey
    })

    let {cartItems} = cartstate;

    let calcTotal = (): number => {
        let total: number = 0;
        for(let cartItem of cartItems){
            total += Number(cartItem.price) * Number(cartItem.qty);
        }
        return total;
    }

    let calcTax = (): number => {
        let a:number = calcTotal() * (tax/100);
        return a;
    }

    let calcGrandTotal = (): number => {
        return (calcTotal() + calcTax())
    }

    let clickIncrQty = (productId: string) => {
        dispatch(orderActions.incrProductQty(productId))
    }

    let clickDecrQty = (productId: string) => {
        dispatch(orderActions.decrProductQty(productId))
    }

    let clickDelQty = (productId: string) => {
        dispatch(orderActions.deleteProductItem(productId))
    }

    return (
        <React.Fragment>
            <section className="mt-3">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className="h3 text-success">Your Cart</p>
                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. At autem blanditiis commodi consequatur corporis distinctio dolores doloribus enim, error harum, labore natus necessitatibus neque quae, rem ullam unde vel voluptas?</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                cartItems.length > 0 ? <React.Fragment>
                    <div className="container">
                        <div className="row">
                            <div className="col-md-8">
                                <div className="card mt-4">
                                    <div className="card-header bg-primary text-light">
                                        <p className="h2">Cart Items</p>
                                    </div>
                                    <div className="card-body">
                                        <table className="table table-success table-striped">
                                            <thead>
                                                <tr>
                                                    <th>Image</th>
                                                    <th>Name</th>
                                                    <th>Qty</th>
                                                    <th>Price</th>
                                                    <th>Action</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    cartItems.map(cartItem => {
                                                        return(
                                                            <tr key={cartItem._id}>
                                                                <td className="align-middle">
                                                                    <img src={cartItem.image} alt="" width={100} height={150}/>
                                                                </td>
                                                                <td className="align-middle">{cartItem.name}</td>
                                                                <td className="align-middle">
                                                                    <i className="fa fa-minus-square mx-2" onClick={clickDecrQty.bind(this, cartItem._id as string)} />
                                                                    {cartItem.qty} 
                                                                    <i className="fa fa-plus-square mx-2" onClick={clickIncrQty.bind(this, cartItem._id as string)}/>
                                                                </td>
                                                                <td className="align-middle">{cartItem.price}</td>
                                                                <td className="align-middle">
                                                                    <button onClick={clickDelQty.bind(this,cartItem._id as string)} className="btn btn-danger"> <i className="fa fa-trash"/></button>
                                                                </td>
                                                            </tr>
                                                        )
                                                    })
                                                }
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="card mt-4">
                                    <div className="card-header bg-primary text-light">
                                        <p className="h2">Your total</p>
                                    </div>
                                    <div className="card-body">
                                        <ul className="list-group">
                                            <li className="list-group-item list list-group-item-success">
                                                <p className="text-black">Total: {calcTotal().toFixed(2)}</p>
                                            </li>
                                            <li className="list-group-item list list-group-item-success">
                                                <p className="text-black">Tax: {calcTax().toFixed(2)}</p>
                                            </li>
                                            <li className="list-group-item list list-group-item-success">
                                                <p className="text-black">Grand Total: {calcGrandTotal().toFixed(2)}</p>
                                            </li>
                                        </ul>
                                        <NavLink to={'/orders/checkout'} className="btn btn-success btn-sm m-2">Checkout</NavLink>
                                        <NavLink to={'/'} className="btn btn-secondary btn-sm m-2">Shop More</NavLink>
                                    </div>
                                </div>                    
                            </div>
                        </div>
                    </div>
                     </React.Fragment> : <React.Fragment>
                        <div className="container">
                            <div className="row">
                                <div className="col">
                                    <div className="text-center">
                                        <p className="h5">Cart is empty</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                </React.Fragment>
            }
        </React.Fragment>
    )
}
export default CartPage;

