import { v4 as uuidv4 } from 'uuid';

export const SHOW_ALERT_MESSAGE: string = 'SHOW_ALERT_MESSAGE';
export const HIDE_ALERT_MESSAGE: string = 'HIDE_ALERT_MESSAGE';

export const showAlertMessage = (color: string, message: string) => {
    let id = uuidv4();
    return (dispatch: any) => {
        try {
            dispatch({
                type: SHOW_ALERT_MESSAGE,
                payload: {
                    id: id,
                    color: color,
                    message: message
                }
            });
            setTimeout(() => {
                dispatch(hideMessage(id));
            }, 2000)
            // dispatch(hideMessage(id))
        } catch (error) {
            console.error(error);
        }
    }
}

export const hideMessage = (id : string) => {
    return (dispatch: any) => {
        try {
            dispatch({
                type: HIDE_ALERT_MESSAGE,
                payload: {
                    id: id
                }
            });
        } catch (error) {
            console.error(error)
        }
    }
}