import React, { useEffect } from 'react';
import * as orderActions from '../../redux/orders/order.action'
import * as orderReducer from '../../redux/orders/order.reducer'
import { useSelector, useDispatch } from 'react-redux';
import Spinner from '../layout/Spinner'

interface IProps{}
interface IState{
    orderKey: orderReducer.OrderState
}

let OrderList:React.FC<IProps> =  ({}) => {
    let disptach = useDispatch();

    let orderState: orderReducer.OrderState = useSelector((store: IState) => {
        return store.orderKey
    })

    let {loading, order} = orderState;

    useEffect(() => {
        disptach(orderActions.getAllOrders())
    }, [])
    
    return (
        <React.Fragment>
            {/* <pre>{JSON.stringify(order)}</pre> */}
            <section>
                <p className="display-6 secheight">
                    <i className="fa fa-list"/> All Orders</p>
            </section>
            <section className="mt-4">
                {
                    loading ? <Spinner/> : <React.Fragment>
                        {
                            order && order.length > 0 && 
                            <div className="container">
                                <table className="table table-hover table-striped text-center shadow-lg">
                                    <thead className="secbg">
                                        <tr>
                                            <th>Order ID</th>
                                            <th>NAME</th>
                                            <th>Email</th>
                                            <th>Items</th>
                                            <th>Amount</th>
                                            <th>Order history</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            order.map(order => {
                                                return(
                                                    <tr key={order._id}>
                                                        <td>{order._id}</td>
                                                        <td>{order.name}</td>
                                                        <td>{order.email}</td>
                                                        <td>
                                                            <ul>{order.items.map(item => {
                                                            return(
                                                                <React.Fragment>
                                                                    <li className="list-group-item ">
                                                                        Name: {item.name} {" "} 
                                                                        Brand: {item.brand} {" "} 
                                                                        Price: {item.price} {""} 
                                                                        Qty: {item.qty} 
                                                                    </li>
                                                                </React.Fragment>
                                                            )
                                                            })}
                                                        </ul>
                                                        </td>
                                                        <td>{order.total}</td>
                                                        <td>{new Date(order.createdAt).toLocaleString()}</td>

                                                    </tr>
                                                )
                                            })
                                        }
                                    </tbody>
                                </table>
                            </div>
                        }
                    </React.Fragment>
                }
            </section>
        </React.Fragment>
    );
}
export default OrderList;

