import React, {useState} from 'react';
import { NavLink, useHistory } from 'react-router-dom';
import * as userActions from '../../redux/users/user.actions';
import { useDispatch } from 'react-redux';

interface IProps{}
interface IState{}

let UserRegister:React.FC<IProps> =  ({}) => {
    
    let dispatch = useDispatch()
    let history = useHistory()

    let [user, setUser] = useState({
        name: '',
        email: '',
        password: '',
    })
    
    let [userError, setUserError] = useState({
        nameError: '',
        emailError: '',
        passwordError: '',
    })

    let validateUserName = (event : React.ChangeEvent<HTMLInputElement>) => {
        setUser({
            ...user,
            name: event.target.value
        })
        let regExp = /^[a-zA-Z0-9_]{4,10}$/;
        !regExp.test(event.target.value) ? setUserError({...userError, nameError: 'Enter valid username'}) : setUserError({...userError, nameError: ''})
    }

    let validateUserEmail = (event: React.ChangeEvent<HTMLInputElement>) => {
        setUser({
            ...user,
            email: event.target.value
        })
        let regExp = /^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/;
        !regExp.test(event.target.value) ? setUserError({...userError, emailError: 'Enter valid email'}) : setUserError({...userError, emailError: ''})
    }

    let validatePassword = (event: React.ChangeEvent<HTMLInputElement>) => {
        setUser({
            ...user,
            password: event.target.value
        })
        let regExp = /^[a-zA-Z0-9_]\w{6,14}$/;
        !regExp.test(event.target.value) ? setUserError({...userError, passwordError: 'Enter valid password'} ) : setUserError({...userError, passwordError: ''})
    }

    let submitRegister = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        dispatch(userActions.registerUser(user, history))
    }
    return (
        <React.Fragment>
            <div className="row">
                <div className="col-md-4"></div>
                <div className="col-md-4">
                    <div className="mt-4">
                        <h2 className="fw-bold text-center ">Online Shopping</h2>
                            <div className="card mt-4 mb-4">
                                <div className="m-4">
                                    <h4 className="fw-light ">Sign-In</h4>
                                    {/* {JSON.stringify(user.name)} */}
                                    {/* {JSON.stringify(userError.nameError)} */}
                                    <form onSubmit={submitRegister}>
                                        <div>
                                            <input
                                            required
                                            value={user.name}
                                            onChange={validateUserName}
                                            type="text" className={`form-control ${userError.nameError.length > 0 ? 'is-invalid' : ''} mb-2`} placeholder="Name"/>
                                            {userError.nameError.length > 0 ? <small className="text-danger">{userError.nameError}</small> : ''}
                                        </div>                           
                                        <div>
                                            <input
                                            required
                                            value={user.email}
                                            onChange={validateUserEmail}
                                            type="email" className={`form-control ${userError.emailError.length > 0 ? 'is-invalid' : ''} mb-2`} placeholder="Email"/>
                                            {userError.emailError.length > 0 ? <small className="text-danger">{userError.emailError}</small> : ''}
                                        </div>
                                        <div>
                                            <input
                                            required
                                            value={user.password}
                                            onChange={validatePassword}
                                            type="password" className={`form-control ${userError.passwordError.length > 0 ? 'is-invalid' : ''} mb-2`} placeholder="Password"/>
                                            {userError.passwordError.length > 0 ? <small className="text-danger">{userError.passwordError}</small> : ''}
                                        </div>
                                        <div>
                                          {/* <button className="btn btn-primary form-control">Register</button>   */}
                                          <input type="submit" className="btn btn-primary form-control mt-2" value="Register"/>
                                        </div>
                                    </form>
                                </div>                                
                            </div>
                            <span className="d-flex justify-content-center align-items-center text-center flex-column">
                                <p className="fw-lighter text-center">Already have an account? 
                                    <NavLink to={'/users/login'} className="text-primary fw-bold text-decoration-none"> Sign-in</NavLink>
                                </p>
                                
                            </span>
                    </div>
                </div>
                <div className="col-md-4"></div>
            </div>
        </React.Fragment>
    );
}
export default UserRegister;

