import React, { useState } from 'react';
import * as productActions from '../../redux/products/product.actions';
import * as userReducer from '../../redux/users/user.reducer';
import {useHistory} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import { Product } from './models/Product';


interface IProps{}
interface IState{
    product: Product
}

interface IUserState{
    userKey: userReducer.UserState
}

let ProductUpload:React.FC<IProps> =  ({}) => {
    let history = useHistory();
    let dispatch = useDispatch();

    let [state, setState] = useState<IState>({
        product: {
            name : '',
            brand : '',
            image : '',
            price : 0,
            qty : 0,
            category : '',
            description : '',
            usage : ''            
        }
    })

    let updateInput = (event: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
        setState({
            product: {
                ...state.product,
                [event.target.name]: event.target.value
            }
        })
    }

    let updateImage = async (event:React.ChangeEvent<HTMLInputElement | any>) => {
        let imageFile:Blob = event.target.files[0];
        let base64Image:string | ArrayBuffer = await convertBase64String(imageFile);
        setState({
            ...state,
            product : {
                ...state.product,
                image : base64Image.toString()
            }
        });
    };

    let convertBase64String = (imageFile:Blob):Promise<string | ArrayBuffer> => {
        return new Promise((resolve, reject) => {
            let fileReader = new FileReader();
            fileReader.readAsDataURL(imageFile);
            fileReader.addEventListener('load', () => {
                if(fileReader.result){
                    resolve(fileReader.result);
                }
                else {
                    reject('Error Occurred');
                }
            })
        });
    };

    let submitUploadProduct = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        dispatch(productActions.uploadProduct(state.product, history))
    }

    let userState: userReducer.UserState = useSelector((store: IUserState) => {
        return store.userKey
    })

    let {isAdmin} = userState.user;

    return (
        <React.Fragment>
            {/* {JSON.stringify(state.product)} */}
            <section className="mt-4">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className=" h2 text-success">
                                Upload Products Here
                            </p>
                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Modi quaerat quis excepturi ipsum repellendus eum facere fuga tempore omnis! Quod reprehenderit nisi hic culpa quo perferendis molestias esse amet deleniti.</p>
                        </div>
                    </div>
                </div>
            </section>
            <section className="mt-4">
                {
                    isAdmin?
                    <React.Fragment>
                        <div className="container">
                            <div className="row">
                                <div className="col-md-8">
                                    <form onSubmit={submitUploadProduct}>
                                        <div>
                                            <input 
                                            required={true}
                                            name={'name'}
                                            value={state.product.name}
                                            onChange={updateInput}
                                            type="text" className="form-control mt-2" placeholder="Name" />
                                        </div>
                                        <div>
                                            <input
                                            required
                                            name={'brand'}
                                            value={state.product.brand}
                                            onChange={updateInput}
                                            type="text" className="form-control mt-2" placeholder="Brand" />
                                        </div>
                                        <div>
                                            <input
                                            required
                                            name={'image'}
                                            onChange={updateImage}
                                            type="file" className="form-control mt-2" id="formFile"/>
                                        </div>
                                        <div>
                                                {
                                                    state.product && state.product.image && state.product.image?.length > 0 ?
                                                        <img src={state.product.image} alt={''} width={'40'} height={'50'}/> : null
                                                }
                                        </div>
                                        <div>
                                            <input
                                            required
                                            name={'price'}
                                            value={state.product.price}
                                            onChange={updateInput}
                                            type="number" className="form-control mt-2" placeholder="Price" />
                                        </div>
                                        <div>
                                            <input
                                            required
                                            name={'qty'}
                                            value={state.product.qty}
                                            onChange={updateInput}
                                            type="number" className="form-control mt-2" placeholder="Qty" />
                                        </div>
                                        <div>
                                            <select
                                            required
                                            name={'category'}
                                            value={state.product.category}
                                            onChange={updateInput}
                                            className="form-control mt-2">
                                                <option value="">Select a Category</option>
                                                <option value="MEN_FASHION">Mens Fashion</option>
                                                <option value="WOMEN_FASHION">Women's Fashion</option>
                                                <option value="KIDS_FASHION">Kids Fashion</option>
                                            </select>
                                        </div>
                                        <div>
                                        <textarea
                                            required
                                            name={'description'}
                                            value={state.product.description}
                                            onChange={updateInput}
                                            placeholder="Description" rows={4} className="form-control mt-2"/>
                                        </div>
                                        <div>
                                            <textarea
                                            required
                                            name={'usage'}
                                            value={state.product.usage}
                                            onChange={updateInput}
                                            placeholder="Usage" rows={4} className="form-control mt-2"/>
                                        </div>
                                        <div className="mt-2">
                                            <input type="submit" className="btn btn-success" value="Upload"/>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </React.Fragment>
                    :
                    <React.Fragment>
                        <div>
                            <div className="container">
                                <div className="row">
                                    <div className="col">
                                        <p className="h5 text-danger">
                                            You are not authorized to to Upload images
                                           <p> <small className="text-danger">Please contact the Application DBA, to grant Admin access</small></p>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </React.Fragment>
                }
            </section>

        </React.Fragment>
    );
}
export default ProductUpload;

