import React from 'react';
import * as userReducer from '../../redux/users/user.reducer';
import * as orderReducer from '../../redux/orders/order.reducer';
import * as orderActions from '../../redux/orders/order.action';
import { useDispatch, useSelector } from 'react-redux';
import { NavLink, useHistory } from 'react-router-dom';
import StripeCheckout from 'react-stripe-checkout'
import stripeImage from '../../assets/img/payment.jpg';
import Spinner from '../layout/Spinner';

const PRODUCT_TAX:number = 5.0;
const stripeAPIKey: string | undefined = "pk_test_51K3bsySARi8rlGu3wGy5XR31osnBZvtKGWj3FCpyj6WjyRpm87BgRe7m8ndwC0EdbyWsuh5SyoJLI10N9Lu8V1Wp00ijuAAj5M";

interface IProps{}
interface IState{
    userKey: userReducer.UserState
}
interface IOrderState{ orderKey : orderReducer.OrderState}

let CheckOutPage:React.FC<IProps> =  ({}) => {

    let dispatch = useDispatch()
    let history = useHistory()

    // get orders info from Store
    let orderState:orderReducer.OrderState = useSelector((store: IOrderState) => {
        return store.orderKey
    });
    
    let useState: userReducer.UserState = useSelector((store: IState) => {
        return store.userKey
    })

    let calcTotal = ():number => {
        let total = 0;
        for(let cartItem of cartItems){
            total += Number(cartItem.price) * Number(cartItem.qty);
        }
        return total;
    };

    let calcTax = ():number => {
        return calcTotal() * PRODUCT_TAX / 100
    };

    let calcGrandTotal = ():number => {
        return calcTotal() + calcTax();
    };

    let handleToken = async (token?:any, addresses?:any) => {
        let items:any = cartItems.map(cartItem => {
            return {
                name : cartItem.name,
                brand : cartItem.brand,
                price:  cartItem.price,
                qty : cartItem.qty
            }
        })

        let order: any = {
            items: items,
            tax: calcTax(),
            total: calcGrandTotal()
        }

        const body = {
            product : {
                name : `${cartItems[0].name} & Others`,
                amount : calcGrandTotal() * 100,
                currency : 'INR',
            },

            customer : {
                          name : addresses.billing_name,
                          address : {
                              line1: addresses.billing_address_line1,
                              postal_code: addresses.billing_address_zip,
                              city: addresses.billing_address_city,
                              state: addresses.billing_address_state,
                              country: addresses.billing_address_country,
                          } },
            description : "Shopping from BrainsKart",
            email : token.email,
            source : token.id,
            stripeTokenType : token.type
    };
      dispatch(orderActions.makeStripePayment(body, order, history));
    }
    
    let {loading, user} = useState
    let {cartItems} = orderState
    
    return (
        <React.Fragment>
            <section className="mb-4">
                <p className="display-6 secheight">Checkout Items</p>
            </section>
            {
                loading ? <Spinner/> :
                <React.Fragment>
                <section className="mt-2 ">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-8">
                                <div className="card">
                                    <div className="card-header secbg">
                                        <p className="h4 fw-bold float-start">Billing Address</p>
                                        <NavLink to={`/users/profile/${user._id}`} className="float-end">
                                            <button className="btn text-light">Edit Address</button>
                                        </NavLink>
                                    </div>
                                    <div className="card-body secheightn">
                                        <ul className="list-group">
                                        {
                                            user && Object.keys(user).length > 0 && user.address &&
                                            <ul className="list-group">
                                                <li className="list-group-item list-group-item-success">
                                                    Flat : <small className="fw-bold">{user.address.flat}</small> <br/>
                                                    Street : <small className="fw-bold">{user.address.street}</small> <br/>
                                                    Landmark : <small className="fw-bold">{user.address.landmark}</small> <br/>
                                                    City : <small className="fw-bold">{user.address.city}</small> <br/>
                                                    State : <small className="fw-bold">{user.address.state}</small> <br/>
                                                    Country : <small className="fw-bold">{user.address.country}</small> <br/>
                                                    Pin : <small className="fw-bold">{user.address.pin}</small> <br/>
                                                    Phone : <small className="fw-bold">{user.address.phone}</small> <br/>
                                                </li>
                                            </ul>
                                        }
                                        </ul>
                                    </div>
                                </div>
                                <div className="card mt-2">
                                    <div className="card-header secbg">
                                        <p className="h4 fw-bold float-start">Payment option</p>
                                    </div>
                                    <div className="card-body secheightn">
                                        <ul className="list-group">
                                            <li className="list-group-item list-group-item-success">
                                                <div className="form-check">
                                                        <input className="form-check-input" type="radio" name="flexRadioDefault"
                                                            id="flexRadioDefault1"/>
                                                        <label className="form-check-label" htmlFor="flexRadioDefault1">
                                                            Cash On Delivery
                                                        </label>
                                                    </div>
                                                    <div className="form-check">
                                                        <input className="form-check-input" type="radio" name="flexRadioDefault"
                                                            id="flexRadioDefault2" checked/>
                                                        <label className="form-check-label" htmlFor="flexRadioDefault2">
                                                            Credit Card Payment
                                                        </label>
                                                </div>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>    
                            <div className="col-md-4">
                                <div className="card">
                                    <div className="card-header secbg">
                                        <p className="h4 fw-bold">Cart Items</p>
                                    </div>
                                    <div className="card-body">
                                                <ul className="list-group">
                                                {
                                                    cartItems.length > 0 &&
                                                        cartItems.map(cartItem => {
                                                            return(
                                                                <li key={cartItem._id} className="list-group-item list-group-item-success">
                                                                    <div className="row">
                                                                        <div className="col-2">
                                                                            <img src={cartItem.image} alt={''} width={50} height={80}/>
                                                                        </div>
                                                                        <div className="col-10 d-flex align-items-center justify-content-center flex-column">
                                                                            <span>Name: <small className="fw-bold">{cartItem.name}</small><br/>
                                                                            Price: <small className="fw-bold">&#8377; {cartItem.price}</small><br/>
                                                                            Qty :  <small className="fw-bold">{cartItem.qty}</small><br/></span>
                                                                            
                                                                        </div>
                                                                    </div>
                                                                </li>
                                                            )
                                                    })
                                                }
                                                </ul>
                                                <ul className="list-group mt-3">
                                                    <li className="list-group-item list-group-item-dark">
                                                        Total : <span className="fw-bold">&#8377; {calcTotal().toFixed(2)}</span>
                                                    </li>
                                                    <li className="list-group-item list-group-item-dark">
                                                        Tax : <span className="fw-bold">&#8377; {calcTax().toFixed(2)}</span>
                                                    </li>
                                                    <li className="list-group-item list-group-item-dark">
                                                        Grand Total : <span className="fw-bold">&#8377; {calcGrandTotal().toFixed(2)}</span>
                                                    </li>
                                                </ul>
                                                <StripeCheckout 
                                                    token={handleToken}
                                                    billingAddress
                                                    stripeKey={stripeAPIKey}
                                                    name="Stripe Payment"
                                                    amount={calcGrandTotal() * 100}
                                                    description="Payments with Stripe"
                                                    currency="INR"
                                                    zipCode
                                                    image={stripeImage}
                                                    panelLabel="Pay for {{amount}}"
                                                    
                                                >
                                                     <button className="btn btn-danger btn-sm mt-2">Pay now {calcGrandTotal()}</button>
                                                </StripeCheckout>
                                               
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div style={{marginBottom:'99px'}}>
                    </div>
                </section>
            </React.Fragment>
            }
            
        </React.Fragment>
    );
}
export default CheckOutPage;

