import React from 'react';
import * as userReducer from '../../redux/users/user.reducer';
import { useDispatch, useSelector } from 'react-redux';
import Spinner from '../layout/Spinner'
import { NavLink } from 'react-router-dom';

interface IProps{}
interface IState{
    userKey: userReducer.UserState
}

let UserProfile:React.FC<IProps> =  ({}) => {

    let dispatch = useDispatch()

    let useState: userReducer.UserState = useSelector((store: IState) => {
        return store.userKey
    })

    let {loading, user} = useState

    return (
        <React.Fragment>
            <section>
                <p className="display-6 secheight">Your Profile</p>
            </section>
            {
                loading ? <Spinner/> : <React.Fragment>
                    {
                        user && Object.keys(user).length > 0 &&
                        <section>
                        <div className="container">
                            <div className="row">
                                <div className="col-md-4">
                                    <img src={user.avatar} className="shadow-lg" width={200} height={200} alt="" />
                                </div>
                                <div className="col-md-8">
                                    <div className="card">
                                        <div className="card-header secbg">
                                            <p className="h4 fw-bold">Your information</p>                                    
                                        </div>
                                    </div>
                                    <div className="card-body secheightn">
                                        <ul className="list-group">
                                            <li className="list-group-item">Name: {user.name}</li>
                                            <li className="list-group-item">Email: {user.email}</li>
                                            {/* <li className="list-group-item">Phone: 9999444401</li> */}
                                        </ul>
                                    </div>
                                    <div className="card mt-4">
                                        <div className="card-header secbg">
                                            <p className="h4 fw-bold float-start">Billing Address</p>
                                            <NavLink to={`/users/profile/${user._id}`} className="btn btn-danger btn-sm float-end">Edit / Add <i className="fa fa-edit"/>
                                            </NavLink>
                                        </div>
                                    </div>
                                    <div className="card-body secheightn">
                                        <ul className="list-group">
                                            <li className="list-group-item">Flat: {user.address?.flat}</li>
                                            <li className="list-group-item">Street: {user.address?.street}</li>
                                            <li className="list-group-item">Landmark: {user.address?.landmark}</li>
                                            <li className="list-group-item">City: {user.address?.city}</li>
                                            <li className="list-group-item">State: {user.address?.state}</li>
                                            <li className="list-group-item">Country: {user.address?.country}</li>
                                            <li className="list-group-item">Pin: {user.address?.pin}</li>
                                            <li className="list-group-item">Mobile: {user.address?.phone}</li>
                                        </ul>                                     
                                    </div>
                                    <NavLink to={'/orders/list'}>
                                        <button className="btn btn-primary">Orders</button>   
                                     </NavLink>
                                </div>
                            </div>
                        </div>
                        </section>
                    }
                    </React.Fragment>
                
            }
            <div style={{marginBottom: '100px'}}>

            </div>
        </React.Fragment>
    );
}
export default UserProfile;

