import React, { useEffect } from 'react';
import * as productActions from '../../redux/products/product.actions';
import * as productReducer from '../../redux/products/product.reducer';
import * as orderActions from '../../redux/orders/order.action';
import { useDispatch, useSelector } from 'react-redux';

import { NavLink } from 'react-router-dom';
import Spinner from '../layout/Spinner'

// import image1 from '../../assets/products/mens/men_1.jpg'
// import image2 from '../../assets/products/mens/men_2.jpg'
// import image3 from '../../assets/products/mens/men_8.jpg'
// import image4 from '../../assets/products/mens/men_19.jpg'
import { Product } from './models/Product';

interface IProps{}
interface IState{
    productKey: productReducer.ProductState
}

let MenFashion:React.FC<IProps> =  ({}) => {

    const category: string = "MEN_FASHION";
    let dispatch = useDispatch();

    let productState: productReducer.ProductState = useSelector((store: IState) => {
        return store.productKey
    });

    useEffect(() => {
        dispatch(productActions.getProducts(category));
    }, [])

    let {products, loading, errorMessage} = productState;

    let clickAddToCart = (product: Product) => {
        dispatch(orderActions.addToCart({
            ...product,
            qty: 1
        }))
    }

    return (
        <React.Fragment>
            <section className="mt-4">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className=" h2 text-success">
                                Men's fashion
                            </p>
                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Modi quaerat quis excepturi ipsum repellendus eum facere fuga tempore omnis! Quod reprehenderit nisi hic culpa quo perferendis molestias esse amet deleniti.</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                loading ? <Spinner/> : <React.Fragment>
                    {
                        products.length >= 0 ? <React.Fragment>
                            <section className="mt-4">
                                <div className="container">
                                    <div className="row">
                                        {
                                            products.map(product => {
                                                return(
                                                    <div className="col-md-3" key={product._id}>
                                                        <div className="card">
                                                            <NavLink to={`/products/${product._id}`}>
                                                                <img src={product.image} alt="" className="m-auto d-block" height={250} width={150}/>
                                                            </NavLink>
                                                            <div className="card-body">
                                                                <ul className="list-group">
                                                                    <li className="list-group-item">
                                                                        Name: <span className="fw-bold">{product.name}</span>
                                                                    </li>
                                                                    <li className="list-group-item">
                                                                        Brand: <span className="fw-bold">{product.brand}</span>
                                                                    </li>
                                                                    <li className="list-group-item">
                                                                        Price: <span className="fw-bold">&#8377; {product.price}</span>
                                                                    </li>
                                                                </ul>
                                                                <button onClick={clickAddToCart.bind(this, product)} className="btn btn-success btn-sm mt-2">Add to Cart</button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                                </div>
                            </section>
                        </React.Fragment> : null
                    }
                </React.Fragment>
            }
            <div style={{marginBottom: '150px'}}></div>
        </React.Fragment>
    );
}
export default MenFashion;

