import * as cartActions from '../orders/order.action'
import { Product } from '../../modules/products/models/Product';
import { incrProductQty, PLACE_ORDER_FAILURE, GET_ORDER_FAILURE } from './order.action';
import { STATES } from 'mongoose';
import { Order } from '../../modules/orders/models/Order';

export interface OrderState{
    cartItems: Product[],
    loading: boolean,
    order: Order[],
    errorMessage: string
}

let initialState: OrderState = {
    cartItems: [] as Product[],
    loading: false,
    errorMessage: '',
    order: [] as Order[]
}

export const reducer = (state = initialState, actions: any): OrderState => {
    switch(actions.type){
        case cartActions.ADD_TO_CART_SUCCESS:
            let existing: Product | undefined = state.cartItems.find(cartItem => cartItem._id === actions.payload.product._id);
            if(existing) return state
            else return {
                ...state,
                cartItems: [...state.cartItems, actions.payload.product]
            }
        case cartActions.ADD_TO_CART_FAILURE:
            return {
                ...state,
                errorMessage: actions.payload.error
            }
        case cartActions.INCR_PRODUCT_QTY:
            let incrProduct: Product[] = state.cartItems.map(cartItem => {
                if(cartItem._id === actions.payload.productId){
                    return {
                        ...cartItem,
                        qty: Number(cartItem.qty) + 1
                    }
                }
                return cartItem;
            });
            return {
                ...state,
                cartItems: [...incrProduct]
            }
        case cartActions.DECR_PRODUCT_QTY:
            let decrProduct: Product[] = state.cartItems.map (cartItem => {
                if(cartItem._id === actions.payload.productId){
                    return {
                        ...cartItem,
                        qty: Number(cartItem.qty) - 1 > 0 ? Number(cartItem.qty) - 1 : 1
                    }
                }
                return cartItem;
            })
            return {
                ...state,
                cartItems: [...decrProduct]
            } 
        case cartActions.DELETE_CART_ITEM:
            let remainingItems: Product[] = state.cartItems.filter(cartItem => cartItem._id !== actions.payload.productId);
            return { 
                ...state,
                cartItems: [...remainingItems]
            }
        // Make payment
        case cartActions.MAKE_STRIPE_PAYMENT_REQUEST:
            return {
                ...state,
                loading: true
            }    
        case cartActions.MAKE_STRIPE_PAYMENT_SUCCESS:
            return {
                ...state,
                loading: false
            }
        case cartActions.MAKE_STRIPE_PAYMENT_FAILURE:
            return {
                ...state,
                loading: false
            }
        // Place order
        case cartActions.PLACE_ORDER_REQUEST:
            return {
                ...state,
                loading: true
            }    
        case cartActions.PLACE_ORDER_SUCCESS:
            return {
                ...state,
                loading: false
            }
        case cartActions.PLACE_ORDER_FAILURE:
            return {
                ...state,
                loading: false,
                errorMessage : actions.payload.error
            }
        case cartActions.CLEAR_CART:
            return {
                ...state,
                cartItems: []
            }
        // Get order details
        case cartActions.GET_ORDER_REQUEST:
            return {
                ...state,
                loading: true
            }
        case cartActions.GET_ORDER_SUCCESS:
            return {
                ...state,
                loading: false,
                order : actions.payload.orders
            }
        case cartActions.GET_ORDER_FAILURE:
            return {
                ...state,
                loading: false,
            }   
        default: return state
    }
        
}