import React, {useEffect , useState} from 'react';
import * as userActions from '../../redux/users/user.actions'
import * as userReducer from '../../redux/users/user.reducer'
import {useSelector , useDispatch} from "react-redux";
import Spinner from '../layout/Spinner'
import { useParams, useHistory } from 'react-router-dom';

interface IProps{}
interface URLparam{
    userId: string
}
interface IState{
    userKey: userReducer.UserState
}

let EditShippingAddress:React.FC<IProps> = ({}) => {

    let dispatch = useDispatch();
    let history = useHistory();

    // let {userId} = useParams<URLparam>();

    let [state, setState] = useState({
        address: {
            flat : '',
            street : '' ,
            landmark : '',
            city : '' ,
            state : '' ,
            country : '' ,
            pin : '' ,
            phone : ''
        }
    })

    let changeorCreateInput = (event: React.ChangeEvent<HTMLInputElement>) => {
        setState({
            address: {
                ...state.address,
                [event.target.name] : event.target.value
            }            
        })
    };

    let formSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        dispatch(userActions.updateShippingAddress(state.address, history))
    }

    let userState: userReducer.UserState = useSelector((store: IState) => {
        return store.userKey;
    })

    useEffect(() => {
        if(user && user.address){
            setState({
                address: {
                    ...state.address,
                    flat: user.address.flat ? user.address.flat : '',
                    street: user.address.street ? user.address.street : '',
                    landmark: user.address.landmark ? user.address.landmark : '',
                    city: user.address.city ? user.address.city : '',
                    state: user.address.state ? user.address.state : '',
                    country: user.address.country ? user.address.country : '',
                    pin: user.address.pin ? user.address.pin : '',
                    phone: user.address.phone ? user.address.phone : ''
                }
            })
        }
    }, []);

    let {loading, user} = userState;


    return (
        <React.Fragment>
            <section className="mb-4">
                <p className="display-6 secheight">Edit Address</p>
            </section>
            <section>
                {
                    loading ? <Spinner/> : 
                    <React.Fragment>
                        <div className="container">
                            <div className="row">
                                <div className="col-md-4">
                                    <form onSubmit={formSubmit}>
                                        <div className="mb-2">
                                            <input
                                            required
                                            name="flat"
                                            value={state.address.flat}
                                            onChange={changeorCreateInput}
                                            type="text" className="form-control" placeholder="Flat"/>
                                        </div>
                                        <div className="mb-2">
                                            <input
                                            required
                                            name="street"
                                            value={state.address.street}
                                            onChange={changeorCreateInput} type="text" className="form-control" placeholder="Street"/>
                                        </div>
                                        <div className="mb-2">
                                            <input
                                            required
                                            name="landmark"
                                            value={state.address.landmark}
                                            onChange={changeorCreateInput}
                                            type="text" className="form-control" placeholder="Landmark"/>
                                        </div>
                                        <div className="mb-2">
                                            <input
                                            required
                                            name="city"
                                            value={state.address.city}
                                            onChange={changeorCreateInput} type="text" className="form-control" placeholder="City"/>
                                        </div>
                                        <div className="mb-2">
                                            <input
                                            required
                                            name="state"
                                            value={state.address.state}
                                            onChange={changeorCreateInput} type="text" className="form-control" placeholder="State"/>
                                        </div>
                                        <div className="mb-2">
                                            <input
                                            required
                                            name="country"
                                            value={state.address.country}
                                            onChange={changeorCreateInput} type="text" className="form-control" placeholder="Country"/>
                                        </div>
                                        <div className="mb-2">
                                            <input
                                            required
                                            name="pin"
                                            value={state.address.pin}
                                            onChange={changeorCreateInput} type="text" className="form-control" placeholder="Pin"/>
                                        </div>
                                        <div className="mb-2">
                                            <input
                                            required
                                            name="phone"
                                            value={state.address.phone}
                                            onChange={changeorCreateInput} type="text" className="form-control" placeholder="Phone"/>
                                        </div>
                                        <div className="mb-2">
                                            <input type="submit" className="btn btn-success" value="Update"/>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </React.Fragment>
                }
            </section>
        </React.Fragment>
    );
}
export default EditShippingAddress;