import React from 'react';
import './App.css';

import * as userActions from './redux/users/user.actions';
import { useDispatch } from 'react-redux';

import {BrowserRouter , Switch , Route} from "react-router-dom";
import NavBar from './modules/layout/NavBar';
import Home from './modules/layout/Home';
import About from './modules/layout/About';
import KidsFashion from './modules/products/KidsFashion';
import MenFashion from "./modules/products/MenFashion";
import WomanFashion from "./modules/products/WomanFashion";
import ProductUpload from './modules/products/ProductUpload';
import ProductDetails from "./modules/products/ProductDetails";
import EditShippingAddress from "./modules/users/editShippingAddress";
import CartPage from "./modules/orders/CartPage";
import CheckOutPage from './modules/orders/CheckOutPage';
import OrderList from "./modules/orders/OrderList";
import UserLogin from "./modules/users/UserLogin";
import UserProfile from './modules/users/UserProfile';
import UserRegister from "./modules/users/UserRegister";
import {useEffect} from 'react';
import Alert from './modules/layout/Alert';


let App = () => {
  
  let dispatch = useDispatch();

  useEffect(() => {
    dispatch(userActions.getUserInfo())
  })

  return (
    <React.Fragment>
      <BrowserRouter>
        <NavBar/>
        <Alert/>
          <Switch>
            <Route path={'/about'}><About/> </Route>
            <Route path={'/fashion/kids'}><KidsFashion/></Route>
            <Route  path={'/fashion/men'}><MenFashion/></Route>
            <Route  path={'/fashion/women'}><WomanFashion/></Route>
            <Route  path={'/products/upload'}><ProductUpload/></Route>
            <Route  path={'/products/:productId'}><ProductDetails/></Route>
            <Route  path={'/users/profile/:userId'}><EditShippingAddress/></Route>
            <Route  path={'/users/login'}><UserLogin/></Route>
            <Route  path={'/users/profile'}><UserProfile/></Route>
            <Route  path={'/users/register'}><UserRegister/></Route>
            <Route  path={'/orders/cart'}><CartPage/></Route>
            <Route  path={'/orders/list'}><OrderList/></Route>
            <Route  path={'/orders/checkout'}><CheckOutPage/></Route>
            <Route path={'/'}><Home/> </Route>
          </Switch>
      </BrowserRouter>
    </React.Fragment>
  );
}

export default App;
