import * as alertActions from './alert.actions'

export interface alertType {
    id: string;
    color: string;
    message: string;
}

export interface alertState {
    alerts: alertType[]
}

let initialState: alertState = {
    alerts: [] as alertType[]
}

export const reducer = (state = initialState, actions: any): alertState => {
    switch(actions.type){
        case alertActions.SHOW_ALERT_MESSAGE:
            return {
                ...state,
                alerts: [
                    ...state.alerts,
                    {
                        id: actions.payload.id,
                        color: actions.payload.color,
                        message: actions.payload.message
                    }
                ]
            }
        case alertActions.HIDE_ALERT_MESSAGE:
            let remAlerts: alertType[] = state.alerts.filter(alert => alert.id !== actions.payload.id);
            return {
                alerts: [...remAlerts]
            }
        default: return state;           
    }
};